//colors
$color_celeste_approx: #cdcdcd;
$color_shark_approx: #292929;
$white: #fff;

//@extend-elements
%vm-position {
  position: absolute;
  left: 10px;
  border-radius: 3px;
  background: $color_shark_approx;
  padding: 5px;
  color: $white;
  cursor: pointer;
  line-height: 20px;
  text-align: center;
  box-sizing: content-box;
}


.jvectormap-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.jvector-map-height {
  height: 400px;
}
.jvectormap-tip {
  position: absolute;
  display: none;
  border: solid 1px $color_celeste_approx;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 3px;
  background: $color_shark_approx;
  color: $white;
  font-family: sans-serif, Verdana;
  font-size: smaller;
  padding: 3px;
}
.jvectormap-zoomin {
  @extend %vm-position;
  width: 20px;
  height: 20px;
  bottom: 10px;
}
.jvectormap-zoomout {
  @extend %vm-position;
  width: 20px;
  height: 20px;
  bottom: 50px;
}
.jvectormap-goback {
  @extend %vm-position;
  bottom: 10px;
  z-index: 1000;
  padding: 6px;
}
.jvectormap-spinner {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: center no-repeat url(data:image/gif;base64,);
}
.jvectormap-legend-title {
  font-weight: 700;
  font-size: 14px;
  text-align: center;
}
.jvectormap-legend-cnt {
  position: absolute;
}
.jvectormap-legend-cnt-h {
  bottom: 0;
  right: 0;
  .jvectormap-legend {
    float: left;
    margin: 0 10px 10px 0;
    padding: 3px 3px 1px;
    .jvectormap-legend-tick {
      float: left;
    }
  }
  .jvectormap-legend-tick {
    width: 40px;
  }
  .jvectormap-legend-tick-sample {
    height: 15px;
  }
  .jvectormap-legend-tick-text {
    text-align: center;
  }
}
.jvectormap-legend-cnt-v {
  top: 0;
  right: 0;
  .jvectormap-legend {
    margin: 10px 10px 0 0;
    padding: 3px;
  }
  .jvectormap-legend-tick-sample {
    height: 20px;
    width: 20px;
    display: inline-block;
    vertical-align: middle;
  }
  .jvectormap-legend-tick-text {
    display: inline-block;
    vertical-align: middle;
    line-height: 20px;
    padding-left: 3px;
  }
}
.jvectormap-legend {
  background: #000;
  color: $white;
  //Instead of the line below you could use @include border-radius($radius, $vertical-radius)
  border-radius: 3px;
}
.jvectormap-legend-tick-text {
  font-size: 12px;
}